.auth-card .ant-card-body {
    padding:0;
    background-color: #d1161f;
    /* IE9, iOS 3.2+ */
    background-image: url(data:image/svg+xml;base64,);
    background-image: -webkit-gradient(linear, 0% 0%, 0% 100%,color-stop(0.277, rgb(221, 35, 44)),color-stop(1, rgb(187, 0, 9)));
    /* Android 2.3 */
    background-image: -webkit-linear-gradient(top,rgb(221, 35, 44) 27.7%,rgb(187, 0, 9) 100%);
    /* IE10+ */
    background-image: -webkit-gradient(linear,left top, left bottom,color-stop(27.7%, rgb(221, 35, 44)),to(rgb(187, 0, 9)));
    background-image: linear-gradient(to bottom,rgb(221, 35, 44) 27.7%,rgb(187, 0, 9) 100%);
    background-image: -ms-linear-gradient(top,rgb(221, 35, 44) 27.7%,rgb(187, 0, 9) 100%);
}
.auth-card .ant-card-body .login-form,
.auth-card .ant-card-body .signup-form {
    padding: 0px 24px 24px 0;
}
.auth-card .ant-tabs .ant-tabs-left-bar {
    width: 220px;
    color: #fff;
    box-sizing: content-box;
}
.auth-card .ant-tabs .ant-tabs-left-bar .ant-tabs-tab {
    margin: 0;
    text-align: left;
    padding-left: 60px;
}
.auth-card .ant-tabs .ant-tabs-left-bar .ant-tabs-tab-active.ant-tabs-tab {
    background: #fff;
    color: #d1161f;
}
.auth-card .ant-tabs-nav .ant-tabs-tab:hover{
    background: #fff;
    color: #d1161f;
}
.auth-card .ant-tabs .ant-tabs-left-content {
    background: #fff;
}
.auth-card .ant-tabs .ant-tabs-left-bar .ant-tabs-ink-bar {
    background: #d1161f;
    left: 0;
    width: 6px;
}
.auth-card.ant-card-bordered {
    border: none;
}

.auth-card header {
    height: 150px;
    display: flex;
    flex-direction: row;
}
.auth-card header .codefen-logo{
    font-family: 'Montserrat';
    font-size: 24px;
    color: #fff;
    width: 316px;
    text-align: center;
    display: block;
    padding-top: 60px;
}
.auth-card header .layout-space {
    background: #fff;
    height: auto;
    width: 100%;
    width: -moz-available;          /* WebKit-based browsers will ignore this. */
    width: -webkit-fill-available;  /* Mozilla-based browsers will ignore this. */
    width: stretch;
}
/* IE8- CSS hack */
@media \0screen\,screen\9 {
    .gradient {
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ffdd232c",endColorstr="#ffbb0009",GradientType=0);
    }
}

/* CODEFEN STYLE */

  .ant-layout.codefen-app {
    background-color: transparent;
  }

  .ant-layout.codefen-app .ant-card {
      width: 720px;
      margin: 60px auto;
  }
  .ant-layout.codefen-app .ant-card label {
      color: #999;
      font-family: Ubuntu;
  }
  .ant-btn {
    position: relative;
    margin-bottom: .3125rem;
    font-size: .875rem;
    text-decoration: none;
    text-transform: lowercase;
    letter-spacing: 0;
    cursor: pointer;
    border: 0;
    outline: 0;
    -webkit-transition: box-shadow .2s cubic-bezier(.4,0,1,1),background-color .2s cubic-bezier(.4,0,.2,1),color .2s cubic-bezier(.4,0,.2,1);
    transition: box-shadow .2s cubic-bezier(.4,0,1,1),background-color .2s cubic-bezier(.4,0,.2,1),color .2s cubic-bezier(.4,0,.2,1);
    will-change: box-shadow,transform;
    color: #fff;
    background-color: transparent;
    border-color: #ccc;
    border-radius: .125rem;
    font-family: Ubuntu;
    font-weight: 500;
  }
  a.ant-btn {
      line-height: 32px;
  }
  .ant-btn.ant-btn-primary {
    background-color: #ff3939;
    color: #fff;
  }
  .ant-btn.ant-btn-default {
    color: #ff3939;
    background-color: transparent;
  }
  .ant-btn.ant-btn-primary:hover {
    color: #ff3939;
    background-color: transparent;
    border: 2px solid #ff3939;
    padding: 0 13px;
  }
  .ant-btn.ant-btn-default:hover {
      color: #fff;
      background-color: #ff3939;
  }
  .codefen-logo {
    font-family: 'Montserrat';
  }
.ant-input {
  border-radius: 0;
  height: 36px;
}
.red {
  color: #ff3939;
}


